export var serialize = function (object) {
    var string = [];
    for (var key in object) {
        if (object.hasOwnProperty(key)) {
            string.push(encodeURIComponent(key) + "=" + encodeURIComponent(object[key]));
        }
    }
    return string.length > 0 ? '?' + string.join('&') : '';
};
